.Home .front {
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(90, 90, 110, 1)),
    color-stop(70%, rgba(200, 120, 130, 0.5)),
    to(rgba(255, 200, 190, 0))
  );
  background: -webkit-linear-gradient(
    rgba(90, 90, 110, 1) 0%,
    rgba(200, 120, 130, 0.5) 70%,
    rgba(255, 200, 190, 0) 100%
  );
  background: -o-linear-gradient(
    rgba(90, 90, 110, 1) 0%,
    rgba(200, 120, 130, 0.5) 70%,
    rgba(255, 200, 190, 0) 100%
  );
  background: linear-gradient(
    rgba(90, 90, 110, 1) 0%,
    rgba(200, 120, 130, 0.5) 70%,
    rgba(255, 200, 190, 0) 100%
  );
}

.Home .front h2 {
  color: rgba(255, 200, 190, 1);
}

.Home .front .subtitle {
  color: rgba(255, 190, 180, 0.8);
}

.Home .front .subsubtitle {
  color: rgba(220, 160, 150, 1);
}

.st2 {
  opacity:0.6;

  fill:#F16C6D;
}



