.About {
  background: var(--pink1);
}

.About .front {
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(210,110,125,1)),
    color-stop(60%, rgba(230,190,180,0.7)),
    to(rgba(250,190,195,0))
  );
  background: -webkit-linear-gradient(
    rgba(210,110,125,1) 0%,
    rgba(230,190,180,0.7) 60%,
    rgba(250,190,195,0) 100%
  );
  background: -o-linear-gradient(
    rgba(210,110,125,1) 0%,
    rgba(230,190,180,0.7) 60%,
    rgba(250,190,195,0) 100%
  );
  background: linear-gradient(
    rgba(210,110,125,1) 0%,
    rgba(230,190,180,0.7) 60%,
    rgba(250,190,195,0) 100%
  );
}

.About .image-bg {
  background-image: url('./images/stripesRed.png');
  background-size: contain;
  background-repeat: no-repeat;
}

.About .front .top {
  color: white;
}

.About .underline {
  color: rgb(230, 150, 150);
  text-decoration: underline;
}

.About small {
  color: rgb(250, 180, 180);
}



